.MenuList {max-width: 900px; margin: 50px auto;}
.MenuList h1 {text-align: center; color: #1d66d3; margin-bottom: 20px;}
.MenuList ul {background: #ddd;}
.MenuList ul li {float: left; width: 25%; text-align: center; border: 1px solid #ccc;}
.MenuList ul li img {width: 100%; opacity: 0.6; transition: all 0.5s;}
.MenuList ul li img:hover {opacity: 1; cursor: pointer;}
.MenuList ul li span {display: block; margin: 10px 0;}

